<script>
export default {
  name: 'My',
  data () {
    return {
      wallet_avatar:'',
      wallet_name:'',
      phone:'',
      defaultAvatar:require('../../assets/img/bg.png'),
      bgImg:require('../../assets/img/bg.png'),
      openGoLogin:false,
      appV:'',
      serverVersion:'',
      showVersionDialog:false,
      iosDownloadUrl:'',
      androidDownloadUrl:'',
      userAgent:'android',
    }
  },
  methods: {
  	clearWallet(){
  		localStorage.removeItem('walletArr');
  		this.$router.push('/')
  	},
  	//获取用户信息
  	getUserInfo(){
  		let _this = this;
   	 	//user/info
   	 	_this.$http.get(_this.$lib.url + 'user/info').then(res => {
					console.log(res);
					if (res.status == 0) {
						_this.userInfo = res.data;
						_this.wallet_avatar = _this.userInfo.avatar;
					}
			}).catch(err => {})
  	},
  	//选择图片
  	setAvatar(){
//		this.$refs.avatarInput.click();
	   	if (!window.plus) return;
	    let app = this;
	    let loading = null;
	    plus.gallery.pick(
	        function (file) {
	        	app.$store.commit('setCropperResult',file); //传值给裁剪页面
            app.$router.push('/cropper/my');
	        } , function ( e ) {
    				console.log( "取消选择图片" );
    			}, {filter:"image"}
    	);
  	},
  	showTips(tip){
  		let _this = this
  		if(localStorage.getItem('phone') == '15570876123'){
  			_this.$router.push('/bankCard');
	  	}else{
	  		_this.$layer.open({
			    content: tip
			    ,skin: 'msg'
			    ,time: 2
		  	});
	  	}
  	},
  	//是否退出登录
  	isLoginOut(){
  		this.openGoLogin=true;
  	},
  	//退出登录
  	loginOut(){
  		this.openGoLogin=false;
  		this.$store.commit('backCoinsList',[]);
  		localStorage.removeItem('chooseCoinsTypeArr');
  		localStorage.removeItem('walletId');
  		localStorage.removeItem('chooseQuotationTypeArr');
  		localStorage.removeItem('auditStatus');
  		localStorage.removeItem('userId');
  		localStorage.token='';
  		localStorage.phone='';
//		localStorage.clear();
  		this.$router.push('/login');
  	},
  	//取消
  	cancel(){
  		this.openGoLogin=false;
  	},
  	//版本更新
versionUpdate(){
	let _this = this;
	if (!window.plus) return;
	plus.runtime.getProperty(plus.runtime.appid, function (inf) {
		let appVersion = inf.version;
		_this.userAgent = navigator.userAgent.toLowerCase();
		_this.$http.get(_this.$lib.url + 'space/version').then(res => {
			console.log(res);
			if (res.status == 0) {
				//服务器返回版本号和下载地址
				_this.serverVersion = res.data.value;
				//苹果下载地址
				if(res.data.download){
					if(res.data.download[1].key == "ios"){
						_this.iosDownloadUrl = res.data.download[1].value;
					}
				}
				//安卓下载地址
				if(res.data.download){
					if(res.data.download[0].key == "android"){
						_this.androidDownloadUrl = res.data.download[0].value;
					}
				}
				if (/iphone|ipad|ipod/.test(_this.userAgent)) {    //苹果手机
					if(_this.serverVersion>appVersion){
						_this.showVersionDialog = true;
					}else{
						plus.nativeUI.alert("当前已是最新版本", function(){
						}, "提示", "确定" );
					}
				} else if (/android/.test(_this.userAgent)) {
//					plus.nativeUI.toast("正在准备环境，请稍后！");
						if(_this.serverVersion>appVersion){
							_this.showVersionDialog = true;
						}else{
							plus.nativeUI.alert("当前已是最新版本", function(){
							}, "提示", "确定" );
						}
				}
			}
		}).catch(err => {})
	})
},
//确认
upgrade(){
	this.showVersionDialog=false;
	let wgtWaiting = null;
	let _this = this;
	if(/iphone|ipad|ipod/.test(_this.userAgent)){
		document.location.href = _this.iosDownloadUrl; //上新fir下载地址或安卓地址
	}else if (/android/.test(_this.userAgent)) {
		wgtWaiting = plus.nativeUI.showWaiting("开始下载");
		var dtask = plus.downloader.createDownload(_this.androidDownloadUrl, {}, function (d, status) {
	    console.log("dddd",d)
	    if (status == 200) {
	        var path = d.filename;//下载apk
	        wgtWaiting.setTitle("开始安装");
	        _this.installApp(wgtWaiting,path)
	    } else {
	        plus.nativeUI.alert('版本更新失败:' + status);
	        wgtWaiting.close();
	    }
		});
		dtask.addEventListener("statechanged", function (download, status) {
        switch (download.state) {
            case 2:
//              wgtWaiting.setTitle("已连接到服务器");
                break;
            case 3:
            		setTimeout(function() {
									var percent = download.downloadedSize / download.totalSize * 100;
									wgtWaiting.setTitle("已下载 " + parseInt(percent) + "%");
								}, 0)
                break;
            case 4:
                wgtWaiting.setTitle("下载完成");
                break;
        }
    });
		dtask.start();
	}
},
//安装app
installApp(wgtWaiting,wgtpath) {
    plus.runtime.install(wgtpath, {}, function (wgtinfo) {
        wgtWaiting.close();
//      plus.nativeUI.alert( "更新完成，须重启应用！", function(){
//					plus.runtime.restart();
//				}, "提示", "确定" );
    }, function (error) {
        wgtWaiting.close();
        plus.nativeUI.alert("应用更新失败！\n" + error.message, function(){
				}, "提示", "确定" );
    });
},
//取消
closeVersionDialog(){
	this.showVersionDialog=false;
}
},
  computed:{
  	
  },
  mounted(){
  	this.phone = localStorage.phone;
  	let _this = this;
  	if (!window.plus) return;
		plus.runtime.getProperty(plus.runtime.appid, function (inf) {
			_this.appV = inf.version;
		})
//	let walletInfo=this.$lib.walletInfo;
//	console.log("my-wallatInfo",walletInfo)
//	if(walletInfo){
//		this.wallet_name=walletInfo.name;
//	}
//	this.getUserInfo();
  },
  watch:{
